import uCharts from '@/components/u-charts/u-charts.min.js'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
var scaleLine = null
var accountRing = null
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '报表',
			cWidth: '',
			cHeight: '',
			rWidth: '',
			rHeight: '',
			pixelRation: 1,
			date: 'day',
			accountRingArr: [],
			top1: ['牛肉', '大葱', '皮蛋豆腐', '金针菇', '拖鞋'],
			top2: ['猪肉', '白菜', '香肠', '羽绒服', '棉裤']
		};
	},
	onLoad() {
		this.cWidth = uni.upx2px(750)
		this.cHeight = uni.upx2px(400)
		this.rWidth = uni.upx2px(550)
		this.rHeight = uni.upx2px(400)
		this.getDayData()
		this.getAccountData()
	},
	onShow() {
	},
	methods: {
		handleDateChange(val) {
			this.date = val
		},
		showSaleLine(canvasId, chartData){
			scaleLine = new uCharts({
				canvasId: canvasId,
				type: 'line',
		    colors: ['#51a9f3', '#ef5a62', '#90dc5d', '#f7d767', '#5cdbd3', '#f29d6e', '#b37fec'],
				fontSize:11,
				padding:[15,15,0,15],
				legend:{
					show:true,
					padding:10,
					lineHeight:11,
					margin:0,
					fontColor:'#808695'
				},
				dataLabel:false,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:this.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					disableGrid:true,
					axisLineColor:'#808695',
					fontColor: '#808695'
				},
				yAxis: {
					gridType:'solid',
					gridColor:'#f3f3f3',
					axisLineColor:'#808695',
					fontColor: '#808695'
				},
				width:this.cWidth * this.pixelRation,
				height: this.cHeight * this.pixelRation,
				extra: {
					line:{
						type: 'straight'
					}
				}
			})
		},
		showAccountRing(canvasId, chartData) {
			accountRing = new uCharts({
				canvasId: canvasId,
				type: 'ring',
				fontSize:11,
				colors: ['#51a9f3', '#ef5a62', '#90dc5d', '#f7d767', '#5cdbd3', '#f29d6e', '#b37fec'],
				legend:{
				    show:true,
				    position:'right',
				    float:'center',
				    itemGap:10,
				    padding:5,
				    lineHeight:26,
				    margin:5,
				    borderWidth :1,
						fontSize: 14
				  },
				title: {
					name: '9999',
					color: '#1c2438',
					fontSize: 20,
					offsetY:-8
				},
				subtitle: {
					name: '收款金额(元)',
					color: '#808695',
					fontSize: 12,
					offsetY:-3
				},
				extra: {
					pie: {
						offsetAngle: -45,
						ringWidth: 10,
						labelWidth:15
					}
				},
				background:'#FFFFFF',
				pixelRatio:1,
				series: chartData.series,
				animation: true,
				width: this.rWidth * this.pixelRation,
				height: this.rHeight * this.pixelRation,
				dataLabel: false
			})
			this.accountRingArr = accountRing.opts.series
		},
		getDayData() {
			let saleData={categories:[], series:[]}
			saleData.categories = ['01:00', '02:00', '03:00', '04:00', '05:00', '06:00']
			saleData.series= [
				{
					name: '销售额',
					data: [100, 130, 150, 130, 120, 80],
					color: '#facc14'
				},
				{
					name: '利润',
					data: [50, 80, 100, 80, 50, 30],
					color: '#2fc25b'
				}
			]
			this.showSaleLine("saleLine", saleData)
		},
		getAccountData() {
			let accountData = { series:[] }
			accountData.series = [
				{ name:'现金', data: 600 },
				{ name:'微信', data: 100 },
				{ name:'支付宝', data: 200 },
				{ name:'信用卡', data: 300 }
			]
			this.showAccountRing("accountRing", accountData)
		}
	}
}
